'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import ProductForm, { ProductFormValues } from '../ProductForm';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL!;

export default function AdminProductCreatePage() {
    const router = useRouter();
    const [submitting, setSubmitting] = useState(false);
    const [error, setError] = useState<string | null>(null);

    const handleSubmit = async (values: ProductFormValues) => {
        setError(null);
        setSubmitting(true);

        try {
            const res = await fetch(`${API_BASE_URL}/products`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    name: values.name,
                    description: values.description,
                    price: values.price,
                    stock: values.stock,
                    thumbnailUrl: values.thumbnailUrl || null,
                    cid: values.cid || null,
                }),
            });

            if (!res.ok) {
                const text = await res.text();
                console.error('Create product failed:', text);
                throw new Error('ÏÉÅÌíà Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
            }

            const created = await res.json();
            router.push(`/admin/products/${created.pid}`);
        } catch (e: any) {
            setError(e.message ?? 'ÏÉÅÌíà Îì±Î°ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        } finally {
            setSubmitting(false);
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-lg font-semibold">ÏÉÅÌíà Îì±Î°ù</h2>
                    <p className="text-xs text-slate-500">
                        ÏÉà ÏÉÅÌíàÏùÑ Îì±Î°ùÌï©ÎãàÎã§. ÌïÑÏàò Ï†ïÎ≥¥(ÏÉÅÌíàÎ™Ö, Í∞ÄÍ≤©, Ïû¨Í≥†, ÏÑ§Î™Ö)Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.
                    </p>
                </div>
                <button
                    type="button"
                    onClick={() => router.push('/admin/products')}
                    className="rounded-full border border-slate-300 px-4 py-2 text-xs text-slate-700 hover:bg-slate-50"
                >
                    Î™©Î°ùÏúºÎ°ú
                </button>
            </div>

            <ProductForm
                mode="create"
                submitting={submitting}
                error={error}
                onSubmit={handleSubmit}
                onCancel={() => router.push('/admin/products')}
            />
        </div>
    );
}
